import {
  Box,
  Button,
  Container,
  CssBaseline,
  Divider,
  Stack,
  Typography,
} from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { useNavigate } from "react-router-dom";
import { Header } from "../../components/Header/Header";
import HomeTable from "../../components/Table/HomeTable";
import { Item } from "../../components/Item/ItemHome";
const theme = createTheme();

export function Home() {
  const navigate = useNavigate();
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Header />
      <Container component="main" maxWidth="xl">
        <Box
          sx={{
            marginTop: 2,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Stack sx={{ width: "100%", mb: 2 }}>
            <Item>
              <Typography variant="h4" gutterBottom component="div">
                IMC: o que ?
              </Typography>
              <Typography variant="body1" gutterBottom>
                O IMC  um clculo simples que permite avaliar se a pessoa est
                dentro do peso que  considerado ideal para a sua altura. Tambm
                conhecido como ndice de Massa Corporal, o IMC  uma frmula
                utilizada por vrios profissionais de sade, incluindo mdicos,
                enfermeiros e nutricionistas, para saber, de uma forma rpida,
                se a pessoa precisa ganhar ou perder peso. Embora seja uma
                ferramenta muito comum, o IMC no  considerada a forma mais
                exata de avaliar o peso, j que no leva em considerao a
                composio corporal. Por isso,  comum que no caso de atletas
                (que possuem uma maior quantidade de massa muscular) seja
                aconselhado o uso de outras tcnicas, como a bioimpedncia, para
                uma avaliao mais detalhada do peso.
              </Typography>
            </Item>
          </Stack>
          <Stack sx={{ width: "100%", mb: 2 }}>
            <Item>
              <Typography variant="h4" gutterBottom component="div">
                Tabela de resultados de IMC
              </Typography>
              <Typography variant="body1" gutterBottom>
                Cada resultado do IMC deve ser avaliado por um profissional de
                sade. No entanto, o card a seguir indica os possveis
                resultados do IMC, de acordo com a Organizao Mundial da Sade,
                sendo que o IMC entre 18,5 a 24,9 representa o peso ideal e o
                menor risco de algumas doenas.
              </Typography>
              <Divider sx={{ m: 2 }} />
              <HomeTable />
            </Item>
          </Stack>
          <Stack sx={{ width: "100%", mb: 2 }}>
            <Item>
              <Typography variant="h5" gutterBottom component="div">
                Voc pode verificar seu IMC em sua pagina Dashboard
              </Typography>
              <Typography variant="subtitle1" gutterBottom component="div">
                Clique no boto abaixo para se redicionar ao Dashboard
              </Typography>
              <Button
                type="submit"
                onClick={() => navigate("/dashboard")}
                variant="contained"
                sx={{ mt: 1, mb: 1, width: "30%" }}
              >
                Ir para seu Dashboard
              </Button>
            </Item>
          </Stack>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
